/**
 * WordPress dependencies
 */
import '@wordpress/dom-ready';
import apiFetch from '@wordpress/api-fetch';

type Settings = {
	readonly currentUrl: string;
	readonly experimentType?: string;
	readonly postId?: number;
	readonly postType?: string;
};

let settings: Settings;
let isCreatingExperimentOrHeatmap = false;

function init( _settings: Settings ): void {
	settings = _settings;

	const newExperimentButton = document.querySelector(
		'#wp-admin-bar-nelio-ab-testing-experiment-create a'
	);
	const newHeatmapButton = document.querySelector(
		'#wp-admin-bar-nelio-ab-testing-heatmap-create a'
	);

	if ( newExperimentButton ) {
		newExperimentButton.addEventListener( 'click', onNewExperimentClick );
	} //end if

	if ( newHeatmapButton ) {
		newHeatmapButton.addEventListener( 'click', onNewHeatmapClick );
	} //end if
} //end init()

function onNewExperimentClick( ev: Event ) {
	ev.preventDefault();

	const { postId, postType, experimentType } = settings;
	if ( ! postId || ! postType || ! experimentType ) {
		return;
	} //end if

	if ( isCreatingExperimentOrHeatmap ) {
		return;
	} //end if
	isCreatingExperimentOrHeatmap = true;

	void apiFetch( {
		path: '/nab/v1/experiment',
		method: 'post',
		data: {
			type: experimentType,
			addTestedPostScopeRule: true,
		},
	} ).then( ( response: { id: number } ) => {
		const experiment = {
			id: response.id,
			type: experimentType,
			alternatives: [
				{ id: 'control', attributes: { postId, postType } },
			],
		};

		void apiFetch( {
			path: `/nab/v1/experiment/${ experiment.id }`,
			method: 'PUT',
			data: experiment,
		} ).then( ( savedExperiment: { experimentUrl: string } ) => {
			window.location.href = savedExperiment.experimentUrl; // phpcs:ignore
		} );
	} );
} //end onNewExperimentClick()

function onNewHeatmapClick( ev: Event ) {
	ev.preventDefault();
	if ( isCreatingExperimentOrHeatmap ) {
		return;
	} //end if
	isCreatingExperimentOrHeatmap = true;

	const { postId, postType, currentUrl } = settings;

	void apiFetch( {
		path: '/nab/v1/experiment',
		method: 'post',
		data: {
			type: 'nab/heatmap',
			addTestedPostScopeRule: false,
		},
	} ).then( ( response: { id: number } ) => {
		const experiment = {
			id: response.id,
			type: 'nab/heatmap',
			trackingMode: postId ? 'post' : 'url',
			trackedPostId: postId ? postId : undefined,
			trackedPostType: postId ? postType : undefined,
			trackedUrl: ! postId ? currentUrl : undefined,
		};

		void apiFetch( {
			path: `/nab/v1/experiment/${ experiment.id }`,
			method: 'PUT',
			data: experiment,
		} ).then( ( savedExperiment: { experimentUrl: string } ) => {
			window.location.href = savedExperiment.experimentUrl; // phpcs:ignore
		} );
	} );
} //end onNewHeatmapClick()

// eslint-disable-next-line @typescript-eslint/no-explicit-any, @typescript-eslint/no-unsafe-member-access
( window as any ).initNabQuickActions = init;
